.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 1px solid black;
  padding: 20px;
  width: 300px;
}

.label {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  gap: 5px;
}

.input {
  font-size: 14px;
  padding: 5px;
  outline-color: rgb(113, 113, 214);
  width: 150px;
}

.form-btn {
  margin-right: auto;
  padding: 5px;
  font-size: 16px;
  border: 1px solid grey;
  border-radius: 8px;
  cursor: pointer;
  background-color: transparent;
}

.form-btn:hover,
.form-btn:focus {
  background-color: lightblue;
  transition: background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}
